.Mypage-container{
  position: relative;
  padding: 0;
  z-index: 5;
  min-width: 320px;
  height: 110vh;
  overflow: hidden;
}

.my-backgrund{
  background-color: #f0f0f0;
}

.my-wadding{
  position: relative;
  text-align: center;
  padding-top: 18vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-attachment: fixed!important;

  h1{
    font-size: 4.5rem;
    font-weight: 500;
  }
}

.my-info{
  width: 90%;
  max-width: 370px;
  margin: 10px auto 0;
  display: flex;
  justify-content: space-between;

  span{
    background: url(data:image/png;base64,) 0 no-repeat;
    display: inline-block;
    padding: 0 0 0 30px;
    font-size: 2.12em;
    font-weight: 100;
  }
}

.sub-info{
  position: relative;
  padding: 2vw 0 0;

  .sub-tab{
    width: 100%;
    border-bottom: 1px solid #444;
    overflow-x: auto;
    white-space: nowrap;

    ul{
      width: max-content;
      margin: 0 auto;
      max-width: 1200px;
      overflow-x: auto;
    }

    li{
      float: left;
      text-align: center;
      padding: 0 2vw;
      list-style: none;
    }

    a{
      display: inline-block;
      text-align: center;
      font-weight: 400;
      font-size: 1.12em;
      padding: 20px 10px;
      text-decoration: none;
      color: black;
    }

    a:hover{
      font-weight: 900;
    }

  }
}

.sub-menu{
  background: #fff;

  padding: 60px 0 10px;

  .my-both{
    display: flex;
    width: 97%;
    justify-content: space-between;
    max-width: 1200px;
    margin: 0 auto 0;

    .left-list{
      flex: 0 0 200px;
      margin: 0 0 100px;
      overflow: hidden;
      display: flex;
      flex-wrap: wrap;

      ul{
        list-style: none;
        margin: 0;
        padding: 0;

        li{
          margin: 0 0 20px 0;
          white-space: nowrap;
          cursor: pointer;
          border: 1px solid #ccc;
          border-radius: 30px;
          padding: 8px 21px;
          background: #fff;
          width: 70%;
          text-align: center;
          text-decoration: none;
          display: inline-block;
          font-weight: 300;
          color: #9c9c9c;
          transition: background-color 0.4s ease-in-out;
          
        }

        li:hover{
          background-color: #444;
          color: white;
        }
      }
    }

    .my-content{
      flex: 1 1 0;
      margin: 0 0 50px 80px;
      position: relative;

      h1{
        font-size: 23px;
        font-weight: 500;
        padding: 0 0 20px;
      }
    }
  }
}

.my-content-main{
  position: relative;
  border: 1px solid #dcdcdc;
  background: #fff;
  padding: 20px 20px;
  margin: 0 0 1vw;

  h2{
    font-size: 22px;
    padding-bottom: 30px;
  }
}